#header{
    position: relative;
}

.header{
    display: flex;
    justify-content: center;
    align-items: flex-start;
}

.header h1{
    font-size: 4rem;
    font-weight: 800;
    line-height: 1.4;
    color: var(--color-white);
    position: relative;
    padding-bottom: 2rem;
}

.header span{
    display: block;
}

h1 span:nth-child(2), h1 span:nth-child(3){
    font-weight: 100;
}

.header h1::after{
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 65%;
    height: 2px;
    background: var(--color-white);
}

.header-left p{
    margin: 3rem 0;
    width: 70%;
    color: #fff;
}

.btn{
    border-radius: 5px;
    margin-left: 1rem;
    font-weight: 500;
    padding: 5px 8px;
    font-size: 1.6rem;
    font-weight: 300;
    font-family: "Poppins", sans-serif;
    cursor: pointer;
}

.btn-white{
    border: 2px solid var(--color-white);
    background: transparent;
    color: #fff;
}

.btn-org{
    border: 2px solid transparent;
    background: orangered;
    color: var(--color-white);
}

.header-right img{
    width: 90%;
}

.floating-icon{
    width: 5rem;
    height: 5rem;
    background: var(--light-blue);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translateX(-50%);
    cursor: pointer;
}

.floating-icon a{
    border: 1px solid #fff;
    width: 5rem;
    height: 5rem;
    border-radius: 50%;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    justify-content: center;
    align-items: center;
}

.mouse{
    transform: translateY(-25%);
    animation: bounce .8s ease infinite alternate;
}

@keyframes bounce {
    0%{
        transform: translateY(-25%);
    }
    100%{
        transform: translateY(25%);
    }
}

@media screen and (max-width:768px){
    .header h1{
        font-size: 3rem;
    }

}

@media screen and (max-width:600px){
    .header{
        flex-direction: column;
    }

    .header-left, .header-right{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
        margin: 0 auto;
    }

    .header-left{
        margin-bottom: 4rem;
    }

    .header h1::after{
        left: 50%;
        transform: translateX(-50%);
    }

    .header-right img{
        width: 70%;
    }
}