@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
:root {
  --font-family: "Poppins", sans-serif;
  --dark-blue: #0a1930;
  --light-blue: #1f93ff;

  --color-white: #fff;
  --dark-text: #333;
  --form-button: #2da5ff;
  --color-grey: #eae6eb;
  --box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

html {
  font-size: 10px;
}

body {
  font-family: "Poppins", sans-serif;
}

section {
  width: 100%;
  padding: 8rem 0;
}

.container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 20px;
}

/* Text styles */
.u-text-small {
  font-size: 1.2rem;
  line-height: 1.4;
  font-weight: 300;
}

.u-text-dark {
  color: #333;
  color: var(--dark-text);
}
.u-text-light {
  color: #333;
  color: var(--dark-text);
}

.u-text-center {
  text-align: center;
}

/* Title */
.u-title {
  text-align: center;
  margin-bottom: 4rem;
}

.u-title h2 {
  font-size: 2.5rem;
  font-weight: 300;
  line-height: 1.4;
  text-transform: uppercase;
  color: #333;
  color: var(--dark-text);
  margin: 1.5rem 0;
}

.u-title p {
  width: 50%;
  min-width: 40rem;
  margin: 0 auto;
  color: #333;
  color: var(--dark-text);
}
.header-bg{
  width: 100%;
  height: 100%;
  min-height: 100vh;
  background: var(--dark-blue);
}
section#download{
    background: linear-gradient(90deg, rgba(45, 165, 255, 1) 45%, rgba(1, 223, 245, 1) 100%);
    text-align: center;
    color: var(--color-white);
}

.download h2{
    font-size: 3rem;
    font-weight: 200;
    line-height: 1.4;
    text-transform: uppercase;
    margin: 1.5rem 0;
    padding-bottom: 1rem;
    position: relative;
}

.download h2::after{
    content: "";
    position: absolute;
    left: 50%;
    bottom: 0;
    width: 10%;
    height: 1px;
    background: var(--color-white);
    transform: translateX(-50%);
}

.download-icons{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 2rem;
}

.download-icon{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 1rem 2rem;
    border: 2px solid var(--color-white);
    border-radius: 5px;
    width: 12rem;
    padding: 1rem;
    font-size: 1.5rem;
    cursor: pointer;
}
section#subscribe{
    background: url(/socialx-website/static/media/subscribe-bg.66bb18fc.png);
    text-align: center;
    background-size: cover;
    background-attachment: fixed;
    color: #fff;
}

.subscribe h2{
    font-size: 3rem;
    font-weight: 200;
    line-height: 1.4;
    text-transform: uppercase;
    margin: 1.5rem 0;
    padding-bottom: 1rem;
}

form > *{
    width: 100%;
    max-width: 50rem;
    margin: 3rem auto;
}

form input{
    padding: 1rem;
    background: transparent;
    border: 1px solid #777;
    border-radius: 5px 0 0 5px;
    color: #fff;
    outline: none;
    width: 80%;
    font-size: 1.5rem;
    font-family: "Poppins", sans-serif;
}

form button{
    padding: 1rem;
    background: var(--form-button);
    border-radius: 0 5px 5px 0;
    border: 1px solid var(--form-button);
    cursor: pointer;
    color: #fff;
    font-size: 1.5rem;
    font-weight: 400;
    font-family: "Poppins", sans-serif;
}

.social-icons{
    display: flex;
    justify-content: center;
    align-items: center;
}

.social-icon{
    margin: 2rem;
    padding: 1rem;
    width: 4rem;
    height: 4rem;
    border: 1px solid #fff;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2rem;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}

.social-icon:hover{
    background: var(--light-blue);
}

@media screen and (max-width: 768px){
    form input{
        width: 60%;
    }
}

.question{
    border: 1px solid var(--light-blue);
    border-radius: 3px;
    width: 90%;
    max-width: 60rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
    margin-bottom: 2rem;
    box-shadow: var(--box-shadow);
}

.question-title{
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
}

.question-title h4{
    font-size: 1.4rem;
    font-weight: 600;
    line-height: 1.4;
    text-transform: uppercase;
}

button.question-icon{
    border: 1px solid var(--color-grey);
    height: 3rem;
    width: 3rem;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}
.features-content{
    display: flex;
    justify-content: center;
}

.features-left{
    flex: 1 1;
    text-align: center;
}

.features-left img{
    width: 65%;
}

.features-right{
    flex: 1 1;
}

@media screen and (max-width: 768px){
    .features-content{
        flex-direction: column;
    }
}
.feature{
    display: flex;
    align-items: flex-start;
    margin-bottom: 2rem;
}

.feature-icon{
    position: relative;
    margin-right: 2rem;
}

.inner-icon{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.feature-text h3{
    font-size: 2rem;
    font-weight: 300;
    line-height: 1.4;
    text-transform: uppercase;
    color: var(--light-blue);
}
footer#footer{
    background: var(--dark-blue);
    color: #fff;
    width: 100%;
    padding: 5rem 0;
}

.footer{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));
    grid-gap: 2rem;
}

.footer h4{
    font-size: 1.6rem;
    font-weight: 500;
    line-height: 1.4;
    text-transform: uppercase;
    margin-bottom: 1rem;
}



.footer-links{
    display: flex;
    flex-direction: column;
}

a{
    font-size: 1.2rem;
    color: #fff;
    text-decoration: none;
}

a:hover{
    color: orangered;
}

.footer-contact p{
    display: flex;
    align-items: center;
    margin-bottom: 5px;
}

.footer-box img{
    margin-top: 4rem;
    cursor: pointer;
}


@media screen and (max-width: 768px){
    .footer-box img{
        width: 80%;
    }
}
#header{
    position: relative;
}

.header{
    display: flex;
    justify-content: center;
    align-items: flex-start;
}

.header h1{
    font-size: 4rem;
    font-weight: 800;
    line-height: 1.4;
    color: var(--color-white);
    position: relative;
    padding-bottom: 2rem;
}

.header span{
    display: block;
}

h1 span:nth-child(2), h1 span:nth-child(3){
    font-weight: 100;
}

.header h1::after{
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 65%;
    height: 2px;
    background: var(--color-white);
}

.header-left p{
    margin: 3rem 0;
    width: 70%;
    color: #fff;
}

.btn{
    border-radius: 5px;
    margin-left: 1rem;
    font-weight: 500;
    padding: 5px 8px;
    font-size: 1.6rem;
    font-weight: 300;
    font-family: "Poppins", sans-serif;
    cursor: pointer;
}

.btn-white{
    border: 2px solid var(--color-white);
    background: transparent;
    color: #fff;
}

.btn-org{
    border: 2px solid transparent;
    background: orangered;
    color: var(--color-white);
}

.header-right img{
    width: 90%;
}

.floating-icon{
    width: 5rem;
    height: 5rem;
    background: var(--light-blue);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translateX(-50%);
    cursor: pointer;
}

.floating-icon a{
    border: 1px solid #fff;
    width: 5rem;
    height: 5rem;
    border-radius: 50%;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    justify-content: center;
    align-items: center;
}

.mouse{
    transform: translateY(-25%);
    animation: bounce .8s ease infinite alternate;
}

@keyframes bounce {
    0%{
        transform: translateY(-25%);
    }
    100%{
        transform: translateY(25%);
    }
}

@media screen and (max-width:768px){
    .header h1{
        font-size: 3rem;
    }

}

@media screen and (max-width:600px){
    .header{
        flex-direction: column;
    }

    .header-left, .header-right{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
        margin: 0 auto;
    }

    .header-left{
        margin-bottom: 4rem;
    }

    .header h1::after{
        left: 50%;
        transform: translateX(-50%);
    }

    .header-right img{
        width: 70%;
    }
}
.navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 8rem;
    background: var(--dark-blue);
}

.logo{
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.logo-text{
    font-size: 4rem;
    font-weight: 300;
    color: var(--color-white);
}

.logo-text span{
    font-weight: 600;
    color: orangered;
}

.nav-links{
    display: flex;
}

.nav-links li{
    margin-left: 1rem;
    list-style: none;
}

.nav-links a{
    font-size: 1.6rem;
    font-weight: 300;
    color: var(--color-white);
    text-decoration: none;
    margin-right: 1rem;
}

.nav-links a:hover{
    color: orangered;
}

a.btn{
    border-radius: 5px;
    margin-left: 1rem;
    font-weight: 500;
    padding: 5px 8px;
}

a.btn-dark{
    border: 2px solid var(--color-white);
    background: transparent;
}

a.btn-orange{
    border: 2px solid transparent;
    background: orangered;
    color: var(--color-white);
}

.menu-icons{
    display: none;
}

@media screen and (max-width: 768px){
    .nav-links{
        display: none;
        border: 2px solid var(--color-white);
        z-index: 111;
    }

    .menu-icons{
        display: block;
        cursor: pointer;
    }

    #nav-links-mobile, #nav-links-mobile-hide{
        display: block;
        position: absolute;
        left: 0;
        top: 8rem;
        width: 100%;
        background: var(--dark-blue);
        border-top: 1px solid var(--color-white);
        padding-bottom: 5px;
        transition: all 0.3s;
    }

    #nav-links-mobile{
        animation: slide-in 0.5s ease-in-out;
    }

    @keyframes slide-in{
        0%{
            transform: translateX(-100%);
        }
        100%{
            transform: translateX(0);
        }
    }

    @keyframes slide-in{
        0%{
            transform: translateX(0);
        }
        100%{
            transform: translateX(-100);
        }
    }

    #nav-links-mobile-hide{
        left: -100%;
        animation: slide-out 0.5s ease-in-out;
    }


    #nav-links-mobile li, #nav-links-mobile-hide li{
        text-align: center;
        padding: 1rem 0;
        border-bottom: 1px solid #fff;
        margin: 0 3rem;
    }

    #nav-links-mobile li:last-child, #nav-links-mobile-hide li:last-child{
        border-bottom: none;
    }
}


